<template>
  <div class="gdt">
    <img
      :src="headerImg"
      alt="" />
    <p class="headerTip">
      你离财富自由只差这1步
    </p>
    <div class="formPay">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          type="tel"
          v-model="form.phone"
          name="phone"
          placeholder="请输入手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix">+86<span class="icon">▼</span></span>
          </template>
        </VanField>
        <VanField
          v-show="isShowCode"
          v-model="form.code"
          name="code"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown
                ref="formCount"
                :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <div class="btn" @click="submit">立即报名</div>
      </VanForm>
      <div class="formTip">- 开课3天内，不满意全额退款 -</div>
    </div>
    <img
      v-for="(url, k) in detailImg"
      :key="k"
      :src="url"
      alt="" />
    <!-- <div class="footerTop">
      <div class="countDown">
        优惠倒计时
        <VanCountDown
          :time="countDown.time"
          :format="countDown.format">
          <template #default="timeData">
            <span class="countTime">{{ timeData.minutes }}</span>
            <span class="countUnit">分</span>
            <span class="countTime">{{ timeData.seconds }}</span>
            <span class="countUnit">秒</span>
          </template>
        </VanCountDown>
      </div>
      <div class="rest">剩余优惠名额666</div>
    </div> -->
    <div class="footer">
      <p>
        <span class="only">只要</span>
        <span class="price">12</span>
        <span class="unit">元</span>
        <!-- <span class="priceThroughLine">指导价:￥199</span> -->
      </p>
      <a class="submitBtn" @click="submit">立即报名</a>
    </div>
    <BackDiaLog
      @confirm="pay"
      @cancel="stop"
      v-model="showDialog" />
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import BackDiaLog from '@/components/common/StayDialog' // 支付挽留弹框
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import { codeMap } from './ErrorMsg'
import Store from '@/store'

export default {
  components: {
    BackDiaLog,
    PayTypeDialog,
    CountDown
  },
  data: function () {
    return {
      countDown: {
        time: 60 * 60 * 1000,
        format: 'mm 分 ss 秒'
      },
      form: {},
      showPaySelet: false,
      showDialog: false,
      headerImg: 'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/header.png',
      detailImg: [
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p2.png',
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p3.png',
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p4.png',
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p5.png',
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p6.png',
        'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/p7.png'
      ],
      isShowCode: false // 是否显示验证码输入框
    }
  },
  async beforeRouteEnter (to, form, next) {
    const headLineLog = localStorage.getItem('headLineLog') || '{}'
    Store.$log.update({
      alternate: headLineLog
    })
    next()
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('HeadlineC-phone'),
      code: localStorage.getItem('HeadlineC-code')
    }
    if (this.form.phone && this.form.code) this.isShowCode = true
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.showDialog = true
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },

  beforeDestroy () {
    clearInterval(this.timer)
  },

  methods: {
    stop () {
      clearInterval(this.timer)
    },
    // 提交表单
    submit () {
      let modulePart = '106_toufang_zsy_0357'
      // 埋获取验证码的点
      if (!this.isShowCode) {
        modulePart = '106_toufang_zsy_0356'
      }
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: modulePart
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err = { msg: '', code: '-1' }) {
      const { msg, code } = err
      if (code === '33005' && !this.isShowCode) {
        this.$refs.formCount.start()
        this.isShowCode = true
        return
      }
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.showDialog = false
          if (code === 30006) {
            return this.goSucccess()
          }
          return this.showMsg(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      if (this.isShowCode) {
        this.$log.click({
          current_page: location.href,
          module_ori: '表单提交',
          module_part: '106_toufang_zsy_0356'
        })
      }
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('HeadlineC-phone', this.form.phone)
      localStorage.setItem('HeadlineC-code', this.form.code)
      this.$apis.payment
        .payBeforeCheck(this.form)
        .then(async (res) => {
          this.showPaySelet = true
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12',
          ext_param: [{
            domain: JSON.parse(localStorage.getItem('headLineLog')).domain,
            abKey: 'gdt3',
            group: JSON.parse(localStorage.getItem('headLineLog')).ab_group,
            uniqFlag: JSON.parse(localStorage.getItem('headLineLog')).ab_uniq_user
          }]
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.gdt {
  padding-bottom: 180px;
  background: #ffb786;

  .headerTip {
    width: 560px;
    font-size: 40px;
    font-weight: bold;
    color: #ee3c3e;
    text-align: center;
    padding: 50px 0 10px 0;
    margin: 0 auto;
    white-space: nowrap;
  }

  .headerTip::before,
  .headerTip::after {
    content: '';
    display: inline-block;
    width: 46px;
    height: 30px;
    margin-right: 25px;
    background: url('http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineBC/dot.png');
    background-repeat: no-repeat;
    background-size: contain;
  }

  .headerTip::after {
    margin-right: 0;
    margin-left: 20px;
    transform: rotateY(180deg);
  }

  .footerTop {
    position: fixed;
    bottom: 120px;
    width: 750px;
    height: 60px;
    line-height: 60px;
    background: #000;
    opacity: 0.56;
    display: flex;
    justify-content: space-between;
    padding: 0 66px;
    color: #fff;

    .countDown {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: nowrap;
      font-size: 30px;
      font-family: Source Han Sans CN;
      font-weight: 400;

      .countTime {
        font-size: 34px;
        font-family: Source Han Sans CN;
        font-weight: bold;
        color: #fead21;
        padding: 0 10px;
      }

      .countUnit {
        color: #fff;
      }
    }

    .rest {
      font-size: 26px;
      font-family: Source Han Sans CN;
      font-weight: 400;
    }
  }

  .footer {
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    width: 750px;
    height: 120px;
    background: #fff;
    padding: 15px 20px;

    p {
      .only {
        font-size: 36px;
        font-family: Source Han Sans CN;
        font-weight: 500;
        color: #666;
        background: linear-gradient(0deg, #ee2010 0%, #ff7821 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      .price {
        font-size: 76px;
        font-family: Source Han Sans CN;
        font-weight: 800;
        font-style: italic;
        color: #fff4e1;
        padding-right: 10px;
        background: linear-gradient(0deg, #ee2010 0%, #ff7821 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }

      .unit {
        font-size: 36px;
        font-family: Source Han Sans CN;
        font-weight: 500;
        color: #fff4e1;
        background: linear-gradient(0deg, #ee2010 0%, #ff7821 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        padding-right: 20px;
      }

      .priceThroughLine {
        width: 149px;
        height: 25px;
        font-size: 26px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        text-decoration: line-through;
        color: #666;
        line-height: 56px;
      }
    }
  }

  .submitBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 90px;
    background: linear-gradient(0deg, #ff7a21 0%, #ee1f10 100%);
    border-radius: 45px;
    font-size: 40px;
    font-family: Source Han Sans CN;
    font-weight: 500;
    color: #fff3e3;
  }

  img {
    width: 100%;
    vertical-align: top;
  }

  .formPay {
    width: 680px;
    height: auto;
    box-sizing: border-box;
    text-align: center;
    background: #fff;
    box-shadow: 0 5px 24px 0 rgba(252, 181, 130, 0.39);
    border-radius: 10px;
    margin: 31px auto 40px auto;
    padding: 35px;

    .van-form {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .wrap {
      position: relative;
    }

    .btn {
      width: 456px;
      height: 82px;
      line-height: 82px;
      background: linear-gradient(0deg, #ff7a21 0%, #ee1f10 100%);
      border: 3px solid rgba(252, 228, 162, 0.76);
      box-shadow: 0 4px 10px 0 rgba(202, 112, 64, 0.46);
      border-radius: 41px;
      font-size: 32px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      color: #fff;
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      font-size: 32px;
      font-weight: 500;
      line-height: 32px;
      font-family: PingFangSC-Regular;
    }

    /deep/ .van-cell {
      margin-bottom: 30px;
      width: 600px;
      height: 86px;
      background: #f4f4f4;
      border-radius: 14px;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;
      border: 1px solid #d6d6d6;

      &::after {
        display: none;
      }

      .prefix {
        font-size: 32px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #333;
        display: flex;
        align-items: center;

        .icon {
          font-size: 10px;
          margin: 0 10px;
          color: #ccc;
        }

        padding-right: 15px;
      }

      .van-field__control::placeholder {
        font-size: 28px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #999;
      }

      .van-count-down {
        font-size: 32px;
        font-family: Source Han Sans CN;
        font-weight: 400;
        color: #ee3c3e !important;
      }

      .van-field__button {
        width: 200px;
      }
    }

    .formTip {
      font-size: 24px;
      font-family: Source Han Sans CN;
      font-weight: 400;
      color: #666;
      padding-top: 20px;
    }
  }
}
</style>
